<template>
  <div id="task" style="width: 1100px;margin-left: 15%">
    <div style="border-bottom: 2px solid #b3b3b3">
      <h3 style="border-left: 5px solid #7b9e31;">
        &nbsp;进行中({{ count1 }})
      </h3>
    </div>
    <div>
      <h3 style="border-left: 5px solid #9b9b9b;">
        &nbsp;已结束({{ count2 }})
      </h3>
    </div>
  </div>
</template>

<script>
export default {
  name: "Task",
  data() {
    return {
      count1: '0',
      count2: '0',
    }
  }
}
</script>

<style scoped>

</style>



